.sidebar {
    background: #FFF;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.10), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
    position: absolute;
    right: 10000%;
    transition: all 0.5s;
}

.sidebar.open {
    width: 317px;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 999;
    transition: all 0.5s;
}
.background {
    opacity: 0.3;
    background: #000;
}


.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
    margin: 60px 0 0 70px;
}

.nav a {
    color: #000;
    font-family: Montserrat, serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration-line: blink;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: #0059B2;
    text-decoration-line: underline;
}

.close {
}

.close img {
    position: absolute;
    right: 16px;
    top: 16px;
    display: flex;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border: none
}
