.burgerMenuIcon {
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.header {
    height: 60px;
    background: #FFF;
    box-shadow: 0 0 2px 0 rgba(29, 33, 38, 0.10), 0 0 40px 0 rgba(29, 33, 38, 0.13);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header h1 {
    color: #000;
    font-family: Montserrat, serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}