.spanBlock {
    display: flex;
    align-items: center;
    margin-top: 30px;
}

.span {
    color: #0080FF;
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.input {
    width: 276px;
    height: 36px;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    border: 1px solid #D1D1D1;
    outline: none;
}

.pen {
    margin-right: 20px;
}