.users {
    margin-top: 20px;
    max-width: 600px;
    width: 100%;
}

.thead {
    background-color: #E5E5E5;
    padding: 10px;
    height: 35px;
}

.item {
    display: table-row;
    margin: 10px;
    height: 35px;
    border-bottom: 1px solid #ddd;
    position: relative;
}

.item::after {
    content: '';
    position: absolute;
    left: 0;
    top: -2px;
    width: 100%;
    height: 1px;
    background-color: #ddd;
}

.item:last-child {
    border-bottom: none;
}

.buttonsContainer {
    display: flex;
    justify-content: center;
    gap: 10px;
}

.container {
    padding: 10px;
}

.nameCol {
    flex: 1;
}

.ageCol {
    flex: 1;
}
